.portrait {
  border-radius: 100%;
  max-width: 100px;
  border: #757575 solid 1px;
}

article {
  max-width: 600px;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1), 0 10px 25px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  margin: 20px auto;
  background: #383838cc;
  padding: 20px;
}

section {
  margin: -20px;
  background: yellow;
  padding: 10px 20px;
}

section:first-of-type {
  margin-top: 20px;
}

section:last-of-type {
  margin-bottom: 20px;
}

section:last-of-type img.block {
  display: block;
  margin: 20px auto -10px auto;
}

section img.block {
  display: block;
  margin: 20px auto 10px auto;
}

#sites {
  background: #ffffff;
}

#sites a {
  color: #ff7a2d;
}

#apps {
  background: #ff7a2d;
  color: #ffffff;
}

header, footer {
  color: #ffffff;
}

.glyph {
  display: inline-block;
  margin: 10px;
}

.glyph svg {
  width: 40px;
  height: 40px;
}
